import axios from 'axios'
// import qs from 'qs'
import { ElMessage } from 'element-plus'
import router from '@/router/index'
import useUserStore from '@/store/modules/user'

const toLogin = () => {
  useUserStore().logout().then(() => {
    router.push({
      path: '/login',
      query: {
        redirect: router.currentRoute.value.path !== '/login' ? router.currentRoute.value.fullPath : undefined,
      },
    })
  })
}

const api = axios.create({
  baseURL: (import.meta.env.DEV && import.meta.env.VITE_OPEN_PROXY === 'true') ? '/proxy/' : import.meta.env.VITE_APP_API_BASEURL,
  timeout: 1000 * 60,
  responseType: 'json',
})

api.interceptors.request.use(
  (request) => {
    const userStore = useUserStore()
    /**
     * 
     *  token 
     */
    if (userStore.isLogin && request.headers) {
      request.headers.token = userStore.token
    }
    return request
  },
)

api.interceptors.response.use(
  (response) => {
    /**
     * 
     * { status: 1, error: '', data: '' }
     *  status  1  0 
     *  error 
     */
    if (response.status === 401) {
      ElMessage({
        message: '',
        type: 'error',
      })
      toLogin()
    }
    else if (response.status !== 200) {
      //  element plus  Message 
      ElMessage.error({ message: '' })
      return Promise.reject(response.data)
    }
    else if (response.status === 200 && response.data.code === -1) {
      //  element plus  Message 
      ElMessage.error({ message: `${response.data.message}` })
      return Promise.reject(response.data)
    }
    return Promise.resolve(response.data)
  },
  (error) => {
    let message = error.message
    if (message === 'Network Error') {
      message = ''
    }
    else if (message.includes('timeout')) {
      message = ''
    }
    else if (message.includes('Request failed with status code')) {
      message = `${message.substr(message.length - 3)}`
    }
    ElMessage({
      message,
      type: 'error',
    })
    return Promise.reject(error)
  },
)

export default api
